import React from 'react';
import './roomItem.css'
import Avatar from './../helpers/avatar'
import DateItem from './insideRooms/dateItem'
import MessageItem from './insideRooms/messageItem'





export class RoomItem extends React.Component{

    
    componentDidUpdate(prevProps, prevState){ 
    }
    


    /**
     * 
        chat: (120) [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, ]
        date: "2021-04-05T08:26:20.313063Z"
        id: 478846959871
        imgHigh: ""
        imgLow: ""
        name: "606ac9ac1f26298350e253ac"
        userName: "q1"
        users:
            creator: 478846959871
            members: [285584038244]
     */
    componentDidMount() {

  
    }

    messageReads = ()=>{
        const chatroom = this.props.chatroom
        const author = chatroom.chat.filter(e=>e.author !== this.props.userId)
        const count = author.filter(e=>e.read === false).length
        return count
    }


    is_online = () =>{
        const online = this.props.online.find(e=>e===this.props.chatroom.id)
        if(online){
            return(<div className='chatroomJoined'>online</div>)
        }else{
            return(null)
        }
        
    }


    
    
    render(){
        const {chat, imgHigh, imgLow, userName} = this.props.chatroom
        const key = this.props.mapId
        const lastchat = chat[chat.length - 1]
        const author = lastchat.author
        const lastchatTime = lastchat.date
        if(!this.props.chatroom.text){
            return(
                <div className='chatroominListe'>
                        <button className='btnchatroomuser' onClick={()=>this.props.openChat(key)}>
                            <div className='chatroomuser'>
                                <div className='chatroomuserDiv'>
                                    <div className="chatroomImgDiv" >
                                        <Avatar name={userName} imgLow={imgLow} imgHigh={imgHigh}/>
                                        {this.messageReads()===0?null:
                                            <div className='underDateDiv'>
                                                <div className='messageCountDiv'>{this.messageReads()}</div>
                                            </div>
                                        }
                                    </div>
                                    <div className='chatroomSpecsDiv'>
                                        <div className='chatroomTxtDivTop'>
                                            <div className='chatroomProfilName'>{userName}</div>
                                            <DateItem date={lastchatTime}/>
                                        </div>
                                        <div className='chatroomTxtDivBotton'>
                                            <MessageItem message={lastchat.message} author={author} userId={this.props.userId}/>
                                            {this.is_online()}
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                            </button>
                       </div>
            )
        }else{
            return(
                <div className='chatroominListe'>
                    <button className='btnchatroomuser' onClick={()=>this.props.createRoom(key)}>
                        <div className='chatroomuser'>
                            <div className='chatroomuserDiv'>
                                <div className="chatroomImgDiv" >
                                    <Avatar name={userName} imgLow={imgLow} imgHigh={imgHigh}/>
                                </div>
                                <div className='chatroomSpecsDiv'>
                                    <div className='chatroomTxtDivTop'>
                                        <div className='chatroomProfilName'>{userName}</div>
                                    </div>
                                    <div className='chatroomTxtDivBotton'>
                                        <div className='chatroomLastChatDiv' >
                                            ...fragt nach einem gemeinsamen Chatroom
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </button>
                    </div>
            )
        }
        
        
    }
        
    
}

export default RoomItem



  