import React from 'react';
import './imageMessage.css'
import Spinner from './../../helpers/spinner'
import ImageLoader from 'react-load-image';
import ImgView from './../../helpers/dialogViewImg'




function Preloader() {
    return <Spinner loading={true} size={20}/>
}

export class ImageMessage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            imgViewOpen:false,
            imgView:null,

        }  
                   
    }




    

    
    componentDidUpdate(prevProps, prevState){  
    }
    


    /**
     * 
      "author": 24114211353796,
      "chatroomName": "6026c72221e6095ee95f4e76",
      "date": "2021-02-12T18:21:31.627Z",
      "id": 24321794072051,
      "imgHigh": "",
      "imgId": null,
      "imgLow": "",
      "message": "hi",
      "read": true,
     */
    componentDidMount() {

  
    }

    
    imgChatBlase = (imgHigh, imgLow)=>{
        if(imgLow){
            return(
                <ImageLoader
                src={imgLow}
                >
                    <img className='imgBlase'  onClick={()=>this.setState({imgViewOpen: true, imgView: imgHigh})}   alt='...' />
                    <div>Error!</div>
                    <Preloader/>
                </ImageLoader>
                
                
            )
        } 
    }

        

    render(){
        const {imgLow, imgHigh} = this.props
        return(
            <div>
                {this.imgChatBlase(imgLow, imgHigh)}
                <ImgView open={this.state.imgViewOpen} imgUrl={this.state.imgView} close={()=>this.setState({imgViewOpen:false})}/>
            </div>
        )
    }
        
        
        
    
        
    
}

export default ImageMessage;



  