import React from 'react';
import './textMessage.css'
import emojiRegex from './../emoji/emojiRegex'







export class TextMessage extends React.Component{

    
    componentDidUpdate(prevProps, prevState){  
    }
    


    /**
     * 
      "author": 24114211353796,
      "chatroomName": "6026c72221e6095ee95f4e76",
      "date": "2021-02-12T18:21:31.627Z",
      "id": 24321794072051,
      "imgHigh": "",
      "imgId": null,
      "imgLow": "",
      "message": "hi",
      "read": true,
     */
    componentDidMount() {

  
    }

    
    singleEmoji = (mess)=>{
        if (mess.length===2 && mess.match(emojiRegex().join('|'))) {
            return <div className='singleEmoji'>{mess}</div>
        } else {
            return mess
        }
    }

    checkLink=(mess)=>{
        if(mess.startsWith('https://')||mess.startsWith('http://') ){
            return(
                <div>
                    <a href={mess} rel="noopener noreferrer"  target="_blank">{mess}</a>
                </div>
            )
        }else{
            return mess
        }
    }
   

    peruseMessage = (mess)=>{


        let mess_update = this.singleEmoji(mess)
        if(mess_update!==mess){
            return mess_update
        }


        mess_update = this.checkLink(mess)
        if(mess_update!==mess){
            return mess_update
        }



        return mess
    }
    
      



        

    render(){
        const message = this.props.message
        return(
            <div>{this.peruseMessage(message)}</div>
        )
    }
        
        
        
    
        
    
}

export default TextMessage;



  