const examples = [
    {
        "author": 'test1',
        "chatroomName": "ff",
        "date": "2021-02-12T18:21:31.627Z",
        "id": 1,
        "imgHigh": "",
        "imgId": null,
        "imgLow": "",
        "message": "Hey, Archa!",
        "read": true,
    },
    {
        "author": 'test2',
        "chatroomName": "ff",
        "date": "2021-02-12T18:21:31.627Z",
        "id": 1,
        "imgHigh": "",
        "imgId": null,
        "imgLow": "",
        "message": "Moin! was kann kann ich fr dich tuen? ",
        "read": true,
    },
    {
        "author": 'test1',
        "chatroomName": "ff",
        "date": "2021-02-12T18:21:31.627Z",
        "id": 1,
        "imgHigh": "",
        "imgId": null,
        "imgLow": "",
        "message": "ich brauche eine Vernderung",
        "read": true,
    },
    {
        "author": 'test2',
        "chatroomName": "ff",
        "date": "2021-02-12T18:21:31.627Z",
        "id": 1,
        "imgHigh": "",
        "imgId": null,
        "imgLow": "",
        "message": "whle eine neue Farbe ",
        "read": true,
    }
]



export class ColorMenu extends React.Component{
    constructor(props){
        super(props);
        this.state={
            user: this.props.user,
            chatColor : [color.grey,color.highBlue,color.pink,color.lila,color.pastellgreen,color.red, color.white],
            textColor : [color.black,color.white],
            color: {
                backgroundColor: this.props.user.color.backgroundColor,
                bubbleColor: this.props.user.color.bubbleColor,
                fontColor: this.props.user.color.fontColor,
                bubbleColor2: this.props.user.color.bubbleColor2,
                fontColor2: this.props.user.color.fontColor2,

            },
            dialogCheck: false,
            colorChange: false,
            side: 1
        }
    }



    componentDidMount() {
    }
    
    componentWillUnmount() {
    }
    
    close = ()=>{
        if(this.state.colorChange){
            const {color} = this.state 
            const payload = {color}
            this.props.color(payload)
        }
        this.props.openChat()
    }

    changeColor = (colorTaget, pos) =>{
        const {color} = this.state
        switch(pos){
            case 1:
                color.backgroundColor = colorTaget
                this.setState({color: color, colorChange:true})
                return
            case 2:
                color.bubbleColor = colorTaget
                this.setState({color: color, colorChange:true})
                return
            case 4:
                color.fontColor = colorTaget
                this.setState({color: color, colorChange:true})
                return
            case 3:
                color.bubbleColor2 = colorTaget
                this.setState({color: color, colorChange:true})
                return
            case 5:
                color.fontColor2 = colorTaget
                this.setState({color: color, colorChange:true})
                return
            default:
                return 
        }
    }



    
    

    exampleMessage = (messages, index)=>{
        const {author, date, message} = messages
        const {bubbleColor, bubbleColor2, fontColor, fontColor2} = this.state.color
        const user_id = 'test1'
        if(user_id === author){
            // rechte Blase.....
            return(
                <div className='chat_colunm_right' key={index}>
                    <div className='chatblase_right' style={{background:bubbleColor}}>
                        <div className='chatMessage_right' style={{color:fontColor}}>
                            <TextMessage message={message}/>
                            <div className='chatdate_right' >
                                <DateMessage date={date}/>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return(
                <div className='chat_colunm_left' key={index}>
                    <div className='chatblase_left' style={{background:bubbleColor2}}>
                        <div className='chatMessage_left' style={{color:fontColor2}}>
                            <TextMessage message={message}/>
                            <div className='chatdate_left' >
                                <DateMessage date={date}/>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
    }
    
    exampleChat = ()=>{
       const mess =  examples.map((message,index)=>{
            return this.exampleMessage(message, index)
        })
        return mess
    }


    colorMain = ()=>{
        return(
            <div>
                <div className='profileHead'>
                    <button className='profileBackBtn' onClick={()=>this.close()}><GoX size={16}/></button>

                </div>
                <div className='showColorFrame'>
                    <div className='exampleChatDiv' style={{background:this.state.color.backgroundColor}}>
                        {this.exampleChat()}
                    </div>
                </div>
                <div className='profileMainColor'>
                    <div className='profileDataDivColor'>
                        <div className='profileBezColor'>Hintergrund:</div>
                        <div className='profileColorDiv'>
                            {this.state.chatColor.map(e=>
                                <button className={e===this.state.color.backgroundColor?'profileColorBtnState':'profileColorBtn'} key={e} style={{background:e}} onClick={()=>this.changeColor(e,1)}></button>
                        )}</div>
                    </div>
                    <div className='placeholderDiv'></div>
                    <div className='profileDataDivColor'>
                        <div className='profileBezColor'>Chat-Blase rechts:</div>
                        <div className='profileColorDiv'>
                            {this.state.chatColor.map(e=>
                                <button className={e===this.state.color.bubbleColor?'profileColorBtnState':'profileColorBtn'} key={e} style={{background:e}} onClick={()=>this.changeColor(e,2)}></button>
                            )}</div>
                    </div>
                    <div className='placeholderDiv'></div>
                    <div className='profileDataDivColor'>
                        <div className='profileBezColor'>Chat-Blase links:</div>
                        <div className='profileColorDiv'>
                            {this.state.chatColor.map(e=>
                                <button className={e===this.state.color.bubbleColor2?'profileColorBtnState':'profileColorBtn'} key={e} style={{background:e}} onClick={()=>this.changeColor(e,3)}></button>
                            )}</div>
                    </div>
                    <div className='placeholderDiv'></div>
                    <div className='profileDataDivColor'>
                        <div className='profileBezColor'>Text rechts:</div>
                        <div className='profileColorDiv'>
                            {this.state.textColor.map(e=>
                                <button className={e===this.state.color.fontColor?'profileColorBtnState':'profileColorBtn'} key={e} style={{background:e}} onClick={()=>this.changeColor(e,4)}></button>
                            )}</div>
                    </div>
                    <div className='placeholderDiv'></div>
                    <div className='profileDataDivColor'>
                        <div className='profileBezColor'>Text links:</div>
                        <div className='profileColorDiv'>
                            {this.state.textColor.map(e=>
                                <button className={e===this.state.color.fontColor2?'profileColorBtnState':'profileColorBtn'} key={e} style={{background:e}} onClick={()=>this.changeColor(e,5)}></button>
                            )}</div>
                    </div>
                </div>
                <DialogCheck text={this.state.text} open={this.state.dialogCheck} checked={this.callbackDialogcheck}></DialogCheck>
            </div>
        )
    }

    

    render(){
        if(this.props.main===3){
            return (
                <div className='profileScreen'>{this.colorMain()}</div>
            )
        }else{
            return null
        }
        
    }
        
}

export default ColorMenu;



  