import React from 'react';
import './chatverlauf.css'
import api from './../api'
import Message from './message'
import Dateline from './dateline'






function ListItem(props) {
    return (<div>
                <div className= "chatListItem">{props.value}</div>
            </div>);
}



export class Chatbox extends React.Component{
    constructor(props){
        super(props);
        this.state={
            start:false,
            input:'',
            date: new Date(),
            imgLoading: 0,
            chatsLength: this.props.chatroom.chat.length,
            imgCount: this.props.chatroom.chat.filter(e => e.imgLow!==null),
            loadingOldMess: false,
            oldMessage: []

        }
        this.backhandle = this.backhandle.bind(this) 
                   
    }



    shouldComponentUpdate(nextProps, nextState) {
        if(this.props.message_counter !== nextProps.message_counter){
            setTimeout(()=>this.scrollToBottom('smooth'), 100)
            return true
        }
        if(JSON.stringify(this.props.user) !== JSON.stringify(nextProps.user)){
            setTimeout(()=>this.scrollToBottom('auto'), 100)
            return true
        }
        return false

        
        
    }

    
    componentDidUpdate(prevProps, prevState){  
        if(this.props.chatroom.chat.length!==prevProps.chatroom.chat.length){
            //setTimeout(()=>this.scrollToBottom('smooth'), 100)
            
        }
    }
    

    componentDidMount() {
        setTimeout(()=>this.scrollToBottom('auto'), 100)
        
  
    }

    handleScroll = (event) =>{
        var node = event.target;
        const messageCount = this.props.chatroom.chat.length > 120
        const bottom = node.scrollHeight - node.scrollTop === node.clientHeight;
        if (bottom) {
            console.log('am boden');
        }
        if(node.scrollTop < 100 && !this.props.chatroom.archiv && messageCount){
            console.log('oben');
            this.props.chatroom.archiv = true
            this.setState({loadingOldMess: true}, ()=>this.getnextMess())
        }
    }
    
   
    getnextMess = async()=>{
        const name = this.props.chatroom.name
        await api.getNextMessByChatroomName(name).then(res=>{
            if(res.data.next_mess){
                const messlist = res.data.next_mess
                const re_messlist = messlist
                console.log('lade liste.....')
                this.setState({loadingOldMess: false, oldMessage: re_messlist})
                /** 
                re_messlist.forEach(e=> this.props.chatroom.chat.unshift(e))
                this.setState({loadingOldMess: false})
                */
            }
        })
        return
        
    }
    // Dialog schlieen ===>
    backhandle = (e) => {
            this.props.closeMess(e)
    }


    scrollToBottom = (e) => {
        this.messagesEnd.scrollIntoView({ behavior: e, block: 'nearest', inline: 'end' });
        
        
    }
    

    
     


    
      
    chatVerlauf = ()=> {
        const loader = this.props.imgSend
        const {color, id} = this.props.user 
        const lines = [] 
        let dateBefor = ''
        if(this.state.oldMessage.length>0){
            this.state.oldMessage.forEach((element,index) => {
                lines.push(
                    <div>
                        <Dateline message={element} dateBefor={dateBefor} color={color}/>
                        <Message message={element} index={index} color={color} id={id} />
                    </div>
                )
                dateBefor = element.date
                
            });
        }

        this.props.chatroom.chat.forEach((element,index) => {
            lines.push(
                <div>
                    <Dateline message={element} dateBefor={dateBefor} color={color}/>
                    <Message message={element} index={index} color={color} id={id} />
                </div>
            )
            dateBefor = element.date
            
        });
        
        const listItems = lines.map((message) =>
        <ListItem value={message} key={lines.indexOf(message) } />);
        return listItems; 
    }



        

    render(){
        const paneDidMount = (node) => {
            if (node) {
                node.addEventListener('scroll', this.handleScroll);
            }
        };
        return(
            <div className='chatFrame' style={{background:this.props.user.color.backgroundColor}}>
                <div className='chatListVerlauf' ref={paneDidMount}>
                    {this.chatVerlauf()}
                    <div style={{ float:"left", clear: "both" }}
                    ref={(el) => { this.messagesEnd = el; }}>
                    </div>
                </div>
            </div>     
           
        )    
        
        
        
    }
        
    
}

export default Chatbox;



  