import React from 'react';
import './message.css'
import ImageMessage from './insideMessage/imageMessage'
import TextMessage from './insideMessage/textMessage'
import DateMessage from './insideMessage/dateMessage'
import StatusMessage from './insideMessage/statusMessage'






export class Message extends React.Component{

    
    componentDidUpdate(prevProps, prevState){ 
    }
    


    /**
     * 
      "author": 24114211353796,
      "chatroomName": "6026c72221e6095ee95f4e76",
      "date": "2021-02-12T18:21:31.627Z",
      "id": 24321794072051,
      "imgHigh": "",
      "imgId": null,
      "imgLow": "",
      "message": "hi",
      "read": true,
     */
    componentDidMount() {
        

  
    }

    
    
    
    


        

    render(){
        const {author,  date,  imgHigh, imgLow, message, read} = this.props.message
        const {bubbleColor, bubbleColor2, fontColor, fontColor2} = this.props.color
        const user_id = this.props.id
        if(user_id === author){
            // rechte Blase.....
            return(
                <div className='chat_colunm_right'>
                    <div className='chatblase_right' style={{background:bubbleColor}}>
                        <ImageMessage imgHigh={imgHigh} imgLow={imgLow}/>
                        <div className='chatMessage_right' style={{color:fontColor}}>
                            <TextMessage message={message}/>
                            <div className='chatdate_right' >
                                <DateMessage date={date}/>
                                <StatusMessage read={read}/>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return(
                <div className='chat_colunm_left'>
                    <div className='chatblase_left' style={{background:bubbleColor2}}>
                        <ImageMessage imgHigh={imgHigh} imgLow={imgLow}/>
                        <div className='chatMessage_left' style={{color:fontColor2}}>
                            <TextMessage message={message}/>
                            <div className='chatdate_left' >
                                <DateMessage date={date}/>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        
    }
        
        
        
    
        
    
}

export default Message;



  