

import React from 'react';
import './statusMessage.css'
import { FaCheck } from "react-icons/fa";






export class StatusMessage extends React.Component{

    
    componentDidUpdate(prevProps, prevState){  
    }
    


    /**
     * 
      "author": 24114211353796,
      "chatroomName": "6026c72221e6095ee95f4e76",
      "date": "2021-02-12T18:21:31.627Z",
      "id": 24321794072051,
      "imgHigh": "",
      "imgId": null,
      "imgLow": "",
      "message": "hi",
      "read": true,
     */
    componentDidMount() {

  
    }

    
   
    messageStatus=(e)=>{
        switch(e){
            case 'on_way':
                return(
                    <div className='chatMessageStatus'><FaCheck size={8}/></div>
                )
            case false:
                return(
                    <div className='chatMessageStatus'><FaCheck size={8}/><FaCheck size={8}/></div>
                )
            case true:
                return(
                    <div className='chatMessageStatusColor'><FaCheck size={8}/><FaCheck size={8}/></div>
                )
            default:
                break
        }
    }



        

    render(){
        const read = this.props.read
        return(
            <div className='chatMessageDiv'>{this.messageStatus(read)}</div>
                    
        )
    }
        
        
        
    
        
    
}

export default StatusMessage;



  