import React from 'react';
import './index.css'


const dateformat2 = { 
    month: 'short', 
    day: 'numeric',
    //hour12: false,
    //hour:  "2-digit",
    //minute: "2-digit"
};





export class Dateline extends React.Component{

    
    componentDidUpdate(prevProps, prevState){  
    }
    
    /**
     * 
     * messsage == > props
     * 
     * 
     * 
      "author": 24114211353796,
      "chatroomName": "6026c72221e6095ee95f4e76",
      "date": "2021-02-12T18:21:31.627Z",
      "id": 24321794072051,
      "imgHigh": "",
      "imgId": null,
      "imgLow": "",
      "message": "hi",
      "read": true,
     */
    componentDidMount() {

  
    }

    makeALine=()=>{
        let dateLine = this.props.dateBefor
        dateLine = new Date(dateLine).toLocaleString('de-DE', dateformat2)
        const date = this.props.message.date
        if(new Date(date).toLocaleString('de-DE', dateformat2)!==dateLine){
            dateLine = new Date(date).toLocaleString('de-DE', dateformat2)
            return(
                <div className='chatDateLine' style={this.props.color.backgroundColor==='rgb(93, 100, 102)'? null:{color:'black', borderBottomColor: 'black'}}>
                    {dateLine}
                </div>
            )
            }else{
                return null
            }
    }

    
    render(){
        return(
            <div>{this.makeALine()}</div>
        )
          
        
    }
}
export default Dateline
        
        




  